import { TimeSpan } from "./TimeSpan";

export class Signal {

    private _waitHandlers: ((res: boolean) => void)[] = [];
    private _isSet: boolean;
    private _autoReset: boolean;

    /****************************************/

    constructor(isSet: boolean = false, autoReset = true) {
        this._isSet = isSet;
        this._autoReset = autoReset;
    }

    /****************************************/

    set() {
        this._isSet = true;
        for (let i = this._waitHandlers.length - 1; i >= 0; i--)
            this._waitHandlers[i](true);
    }

    /****************************************/

    reset() {
        this._isSet = false;
    }

    /****************************************/

    waitFor(timeout?: TimeSpan): Promise<boolean> {

        if (this._isSet) {
            if (this._autoReset && this._waitHandlers.length == 0)
                this.reset();
            return Promise.resolve(true);
        }

        return new Promise((res) => {

            const handler = isSuccess => {
                res(isSuccess);
                const index = this._waitHandlers.indexOf(handler);
                if (index != -1)
                    this._waitHandlers.splice(index, 1);
                if (this._waitHandlers.length == 0 && this._autoReset)
                    this.reset();
            };

            if (timeout) {
                setTimeout(() => {
                    const index = this._waitHandlers.indexOf(handler);
                    if (index != -1) {
                        this._waitHandlers.splice(index, 1);
                        res(false);
                    }
                }, timeout.totalMilliseconds);
            }


            this._waitHandlers.push(handler);
        });
    }

    /****************************************/

    get isSet() {
        return this._isSet;
    }
}