import "Templates/Spinner.html";

import { delayAsync } from "../../Core/Utils/PromiseUtils";
import { TemplateBuilder } from "../../Templating/TemplateBuilder";
import { IHTMLAttach } from "../Abstraction/IHTMLAttach";
import { registerType } from "../../Core/Utils/ObjectUtils";
import { ContentView } from "./ContentView";
import { IView } from "../Abstraction/IView";
import { View } from "./View";

export class Blocker extends ContentView<IView> implements IHTMLAttach {

    constructor() {

        super({
            template: "ContentViewNested",
            content: new View({template: "Spinner"}),
        });

        this.status = "hide";

        const builder = new TemplateBuilder(this, document.body);

        builder.content(this);
    }

    /****************************************/

    attach(element: HTMLElement) {

        this.element = element;

        element.addEventListener("mousedown", e => e.preventDefault(), { passive: true });
        element.addEventListener("touchstart", e => e.preventDefault(), { passive: true });

        if (this.status == "hide")
            this.element.style.display = "none";
    }

    /****************************************/

    async showAsync(content?: IView) {

        if (this.element)
            this.element.style.removeProperty("display");

        this.status = "showing"

        if (content)
            this.content = content;

        await delayAsync(0);

        if (this.status == "showing")
            this.status = "show";
    }

    /****************************************/

    async hideAsync() {

        this.content = new View({ template: "Spinner" }),

        this.status = "hiding";

        await delayAsync(500);

        if (this.status == "hiding") {
            this.status = "hide";
            if (this.element)
                this.element.style.display = "none";
        }
    }
}

registerType(Blocker, "Blocker");