export function discretize(value: number, steps: number): number {
    if (isNaNOrNull(value))
        return undefined;
    return Math.round(value * steps) / steps;
}

/****************************************/

export function round(value: number, digits: number): number {
    if (isNaNOrNull(value))
        return undefined;

    return Math.round(value * Math.pow(10, digits)) / Math.pow(10, digits);
}

/****************************************/

export function exponential(value: number, weight: number = 2) {
    if (isNaNOrNull(value))
        return undefined;
    return 1 - Math.pow(1 - value, weight);
}

/****************************************/

export function isNaNOrNull(a: number): boolean {
    return isNaN(a) || a === null;
}

/****************************************/

export function sumNull(a: number, b: number): number {

    if (isNaNOrNull(a) && isNaNOrNull(b))
        return undefined;

    if (isNaNOrNull(b))
        return a;

    if (isNaNOrNull(a))
        return b;

    return a + b;
}

/****************************************/

export function divideNull(a: number, b: number): number {
    if (isNaNOrNull(a) || isNaNOrNull(b) || b == 0)
        return undefined;
    return a / b;
}