import { isSmallDevice, isTouchDevice } from "../../Core/Utils/DomUtils";
import { IBehavoir } from "../../Templating/Abstraction/IBehavoir";
import { IAction } from "../Abstraction/IAction";
import { IView } from "../Abstraction/IView";
import { ContextMenu } from "../Components/ContextMenu";

export interface IContextActionProvider {

    getContextActions(): IAction[];
}

/****************************************/

export class ContextMenuBehavoir implements IBehavoir<HTMLElement, IView & IContextActionProvider> {

    attach(element: HTMLElement, viewModel: IContextActionProvider) {

        const showMenu = (ev: MouseEvent | TouchEvent | PointerEvent) => {

            ev.preventDefault();
            ev.stopPropagation();
            ev.stopImmediatePropagation();

            const items = viewModel.getContextActions();
            if (items) {
                const menu = new ContextMenu({ actions: items });
                menu.showAsync(null, ev);
            }

        };

        if (isTouchDevice())
            element.addEventListener("selectstart",ev => {
                ev.preventDefault();
                ev.stopPropagation();
                ev.stopImmediatePropagation();
                return false;
            });

        element.addEventListener("contextmenu", ev => showMenu(ev));

    }

    /****************************************/

    detach(element: HTMLElement) {
    }

    static readonly instance = new ContextMenuBehavoir();
}