import { equals } from "./ObjectUtils";
import { delayAsync } from "./PromiseUtils";

export function equalsArray<T>(a: T[], b: T[]): boolean {

    if (!Array.isArray(a) || !Array.isArray(b))
        return false;

    if (a.length != b.length)
        return false;

    for (let i = 0; i < a.length; i++) {
        if (!equals(a[i], b[i]))
            return false;
    }

    return true;
}
 
/****************************************/

export async function forEachAsync<T>(items: T[], chunkSize: number, action: (item: T, index: number) => void) {

    let index = 0;

    for (const item of items) {

        action(item, index);
        if (index % chunkSize == 0)
            await delayAsync(0);
        index++;
    }
}

/****************************************/

export function mergeArrays<T>(...args: T[][]): T[] {

    const result: T[] = [];
    for (const item of args) {
        if (!item)
            continue;
        result.push(...item);
    }
    return result;
}